<template>
  <div>
    <div class="schedule-table-container">
      <div class="row">
        <div class="schedules" v-if="details!=null">
          <div class="ratio-16-9">
            <iframe  class="ratio-inner" :src="videosource"></iframe>
          </div>

         
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      details: null,
      videoOptions: {
        source: {
          type: "application/x-mpegURL",
          src:
            "https://sfy.zfjm.cc/live/wsstream1492509.m3u8?txSecret=4E3B6D32B83274C3B162E2DF1C03EF71&txTime=5EF8EBF8",
          withCredentials: false
        },
        live: true
      },
      videoConfigs: {
        youtube: true,
        switcher: true,
        hls: true
      }
    };
  },
  computed: {
    videosource() {
      let firstKey = Object.keys(this.details.streaming)[0];
      return (
        "https://ashdajkshdjkhasdkhajksdhk.blogspot.com/?type=hls&src=" +
        encodeURIComponent(this.details.streaming[firstKey].hls)
      );
    }
  },
  created() {
    this.fetchDetails();
  },

  methods: {
    fetchDetails() {
      var vm = this;
      var qs = require("qs");
      var data = qs.stringify({
        mod: "web",
        action: "getMatch",
        id: "232549"
      });
      var config = {
        method: "post",
        url: "https://api.bolalive4.tv/api.php",
        data: data
      };

      this.$http(config)
        .then(function(response) {
          vm.details = response.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>



<style>
.ratio-16-9,
.ratio-16-10,
.ratio-square {
  display: block;
  position: relative;
  width: 100%;
  height: 0;
}

.ratio-16-9 {
  padding-top: 56.25%; /* This is your aspect-ratio */
}

.ratio-16-10 {
  padding-top: 62.5%; /* This is your aspect-ratio */
}

.ratio-square {
  padding-top: 100%; /* This is your aspect-ratio */
}

.ratio-inner {
  display: block;
  position: absolute;

  left: 0;
  top: 0;
  right: 0;
  bottom: 0;

  width: 100%;
  height: 100%;
}
</style>